import { type Theme } from 'gpn-ui'
import { defineStore } from 'pinia'

interface GlobalState {
  theme: Theme
  bg: string
}

export const useGlobalStore = defineStore('global', {
  state: (): GlobalState => {
    return {
      theme: 'kp' as Theme,
      bg: 'transparent'
    }
  },
  actions: {
    switchTheme() {
      this.theme = this.theme === 'kp' ? 'cp' : 'kp'
    },
    setTheme(theme: Theme) {
      this.theme = theme
    },
    setBg(color: string) {
      this.bg = color
    }
  }
})
