import { createApp } from "vue";
import { createRouter, createWebHashHistory } from "vue-router";
import { createPinia } from "pinia";
import routes from "./router";

import App from "./App.vue";
import "bootstrap-icons/font/bootstrap-icons.min.css";
import "./assets/global.sass";
import "gpn-ui/dist/style.css";
// import "../../lib/styles/index.scss";

import "gpn-ui/dist/styles/ripple.scss";
import "gpn-ui/dist/styles/tooltip.scss";

import { createGpn } from "gpn-ui";
// import { createGpn } from "gpn-ui-dev";

const router = createRouter({
    routes,
    history: createWebHashHistory("/"),
});

const pinia = createPinia();

const gpn = createGpn();

createApp(App).use(pinia).use(router).use(gpn).mount("#app");
