    import { GpnSelect, GpnList, GpnNotifyContainer, GpnScrollbar, type ISelectItem, type ListItem, type Theme } from "gpn-ui";
    import { computed, ref } from "vue";
    import { useRouter } from "vue-router";
    import { useGlobalStore } from "./stores/store";

    interface MenuItem extends ListItem {
        to?: string;
    }

    const store = useGlobalStore();
    const selPage = ref<MenuItem | null>(null);
    const bodyScroll = ref<InstanceType<typeof GpnScrollbar>>();
    const router = useRouter();

    const menuItems = ref<MenuItem[]>([
        {
            id: "g-1",
            name: "Кнопки",
            is_group: true,
        },
        {
            id: "g-2",
            name: "Селекты",
            is_group: true,
        },
        {
            id: "g-3",
            name: "Модалки",
            is_group: true,
        },
        {
            id: "g-4",
            name: "Вспомогательные",
            is_group: true,
        },
        {
            id: "avatar",
            group_id: "g-1",
            name: "avatar",
            to: "/avatar",
        },
        {
            id: "avatar-group",
            group_id: "g-1",
            name: "avatar-group",
            to: "/avatar-group",
        },
        {
            id: "button",
            group_id: "g-1",
            name: "Button - (KP, CP)",
            to: "/button",
        },
        {
            id: "badge",
            group_id: "g-1",
            name: "Badge - (KP, CP)",
            to: "/badge",
        },
        {
            id: "checkbox",
            group_id: "g-1",
            name: "Checkbox - (KP, CP)",
            to: "/checkbox",
        },
        {
            id: "checkbox-group",
            group_id: "g-1",
            name: "Checkbox in group - (KP, CP)",
            to: "/checkbox-group",
        },
        {
            id: "radio",
            group_id: "g-1",
            name: "Radio - (KP, CP)",
            to: "/radio",
        },
        {
            id: "switch",
            group_id: "g-1",
            name: "Switch - (KP, CP)",
            to: "/switch",
        },
        {
            id: "switch-group",
            group_id: "g-1",
            name: "Switch in group - (KP, CP)",
            to: "/switch-group",
        },
        {
            id: "loader",
            group_id: "g-1",
            name: "Loader - (KP, CP)",
            to: "/loader",
        },
        {
            id: "text-field",
            group_id: "g-1",
            name: "TextField - (KP, CP)",
            to: "/text-field",
        },
        {
            id: "tooltip",
            group_id: "g-1",
            name: "Tooltip - (KP, CP)",
            to: "/tooltip",
        },
        {
            id: "chips",
            group_id: "g-1",
            name: "Сhips - (KP, CP)",
            to: "/chips",
        },
        {
            id: "select",
            group_id: "g-2",
            name: "Select - (KP, CP)",
            to: "/select",
        },
        {
            id: "choice",
            group_id: "g-2",
            name: "Choice - (KP, CP)",
            to: "/choice",
        },
        {
            id: "context",
            group_id: "g-2",
            name: "Context - (KP, CP)",
            to: "/context",
        },
        {
            id: "sidebar",
            group_id: "g-3",
            name: "Sidebar - (KP, CP)",
            to: "/sidebar",
        },
        {
            id: "dialog",
            group_id: "g-3",
            name: "Dialog - (KP, CP)",
            to: "/dialog",
        },
        {
            id: "tabs",
            group_id: "g-2",
            name: "Tabs - (KP, CP)",
            to: "/tabs",
        },
        {
            id: "notify",
            group_id: "g-3",
            name: "Notify - (KP, CP)",
            to: "/notify",
        },
        {
            id: "paging",
            group_id: "g-2",
            name: "Paging - (KP, CP)",
            to: "/paging",
        },
        {
            id: "date",
            group_id: "g-1",
            name: "Date - (KP, CP)",
            to: "/date",
        },
        {
            id: "period",
            group_id: "g-1",
            name: "Period",
            to: "/period",
        },
        {
            id: "collapse",
            group_id: "g-4",
            name: "Collapse - (KP, CP)",
            to: "/collapse",
        },
        {
            id: "tree",
            group_id: "g-2",
            name: "Tree",
            to: "/tree",
        },
        {
            id: "tree-select",
            group_id: "g-2",
            name: "TreeSelect",
            to: "/tree-select",
        },
        {
            id: "overlay",
            group_id: "g-4",
            name: "Overlay - (KP, CP)",
            to: "/overlay",
        },
    ]);

    const themeOptions = [
        {
            id: "kp",
            name: "KP",
        },
        {
            id: "cp",
            name: "CP",
        },
    ];
    const bgOptions = [
        {
            id: "transparent",
            name: "Trans",
        },
        {
            id: "#F4F6F9",
            name: "grey-9",
        },
        {
            id: "#002033",
            name: "dark",
        },
    ];
    const selTheme = computed(() => {
        return themeOptions.find((f) => f.id === store.theme) || null;
    });
    const selBg = computed(() => {
        return bgOptions.find((f) => f.id === store.bg) || null;
    });

    const clickItem = (item: MenuItem) => {
        if (item.to) router.push(item.to);
    };

    function onThemeSelect(sel: ISelectItem) {
        store.setTheme(sel.id as Theme);
    }
    function onBgSelect(sel: ISelectItem) {
        store.setBg(sel.id as string);
    }

    router.afterEach(async (to, from) => {
        bodyScroll.value?.scrollElem?.scrollTo(0, 0);
    });
</script>

<template>
    <GpnNotifyContainer :theme="store.theme" />
    <main class="main-container">
        <nav class="menu-container">
            <div class="title">GPN UI</div>
            <GpnScrollbar class="menu-list">
                <GpnList v-model="selPage" :items="menuItems" with-groups @click="clickItem">
                    <template #default="{ item, isSelected }: { item: MenuItem; isSelected: boolean }">
                        <div :style="{ color: isSelected ? '#0078d2' : '#002033' }" style="display: grid; padding: 6px 0">
                            <div :style="{ fontWeight: item.is_group ? 600 : 400 }" style="font-size: 14px">{{ item.name }}</div>
                            <div v-if="!item.is_group" style="font-size: 12px; color: #667985">{{ item.to }}</div>
                        </div>
                    </template>
                </GpnList>
            </GpnScrollbar>
        </nav>
        <div class="body-container" :style="{ backgroundColor: store.bg }">
            <header style="padding: 12px 0">
                <div style="width: 320px">
                    <GpnSelect :model-value="selTheme" label="Тема" :options="themeOptions" @select="onThemeSelect" />
                </div>
                <div style="width: 320px">
                    <GpnSelect :model-value="selBg" label="Background" :options="bgOptions" @select="onBgSelect">
                        <template #default="{ item, isSelected }">
                            <div style="display: grid; gap: 8px; grid-template-columns: 16px 1fr; line-height: 16px; align-items: center" :style="{ color: isSelected ? '#0078d2' : '#002033' }">
                                <div class="box-color" :style="{ backgroundColor: String(item.id) }"></div>
                                <div>{{ item.name }}</div>
                            </div>
                        </template>
                    </GpnSelect>
                </div>
            </header>
            <GpnScrollbar ref="bodyScroll">
                <router-view />
            </GpnScrollbar>
        </div>
    </main>
</template>

